/* App.css */
* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

html {
  background-image: url("https://ubuntuhandbook.org/wp-content/uploads/2021/07/M-Maggs-pixabay.jpg");
  background-size: cover;
}

.app-name {
  font-size: 2rem;
  color: rgb(242, 246, 246);
  margin-bottom: 16px;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 600px;
  min-height: 440px;
  background-color: #ffffff67;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
  box-shadow: 10px 14px 18px rgba(19, 2, 2, 0.548);
}

.city-search {
  width: 100%;
  max-width: 400px;
  box-sizing: border-box;
  border: 2px solid rgb(204, 204, 204);
  outline: none;
  border-radius: 20px;
  font-size: 16px;
  background-color: #e5eef0;
  background-position: 10px 12px;
  background-repeat: no-repeat;
  padding: 12px 40px 12px 40px;
  -webkit-transition: width 0.4s ease-in-out;
  transition: width 0.4s ease-in-out;
  color: #060505;
}

.city-search:focus {
  width: 100%;
}

.city-name {
  font-size: 1.5rem;
  color: #0b0303;
  margin-bottom: 8px;
}

.date {
  font-size: 1.25em;
  font-weight: 500;
  color: #070000;
}

.icon-temp {
  font-size: 3rem;
  font-weight: 700;
  color: #080809;
  text-align: center;
}

.deg {
  font-size: 1.3rem;
  vertical-align: super;
}

.des-wind {
  font-weight: 500;
  color: #0e0909;
}

.error-message {
  display: block;
  text-align: center;
  color: #d32f2f;
  font-size: 24px;
  margin-top: auto;
}

.Loader {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.Loader > div {
  margin: 0 auto;
}

.weather-icon {
  display: flex;
  justify-content: center;
  align-items: center;
}

.weather-icon img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}
